import tagData from "../../assets/tagData"
import Ctag from "./Ctag"
export default {
  name: 'catalog',
  props:{
    catalogName:{
      type:String,
      default:""
    }
  },
  data(){
    return {
      tags:tagData[this.catalogName]
    }
  },
  computed:{
    catalogTitle(){
      if(this.catalogName=="district"){
        return "地区"
      }else if(this.catalogName=="winetype"){
        return "红酒类型"
      }else if(this.catalogName=="grapetype"){
        return "葡萄品种"
      }
    },
  },
  components:{
    Ctag
  },
  mounted(){

  }
}



