export default {
  name: 'stag',
  props:{
    showName:{
      type:String,
      default:""
    },
    catalogName:{
      type:String,
      default:""
    },
    isSelect:{
      type:Boolean,
      default:false
    },
    tagIndex:{
      type:Number,
      default:0
    },
    isExpress:{
      type:Boolean,
      default:false
    },
    selectedId:{
      type:String,
      default:""
    },
  },
  data(){
    return {

    }
  },
  methods:{
    StagClickHandle(){
     // alert("A");
      let catalogName=event.target.getAttribute('catalogName');
      let tId=event.target.getAttribute('tId');
      this.$store.dispatch("modifySelectedArr",{catalogName:catalogName,tId:tId})
    }
  },
  computed:{
    isLong(){
      return this.showName.length>5?true:false
    },
    isShow(){
      if(!this.isExpress && this.tagIndex>8){
        return false
      }else{
        return true
      }
    },

    isActive(){
      if(this.selectedId==this.showName){
        return true
      }else{
        return false
      }
    }
  },
  components:{

  },
  mounted(){
  }
}



